:root {
    --calendar-width: 344px;
    --calendar-height: 340px;

    /*      */
    --calendar-selector-button-width: 100px;
    --calendar-selector-button-gap: 3px;
    --calendar-selector-button-outline-border-color: var(--color-light-border-key);

    /*  */
    --calendar-arrow-background: url('https://alfabank.st/icons/icon_arrow-left_m_black.svg');
    --calendar-select-icon-background: url('https://alfabank.st/icons/icon_arrow-select_m_black.svg');
    --calendar-header-paddings: var(--gap-m) var(--gap-m) var(--gap-s);
    --calendar-month-only-header-paddings: var(--gap-m) var(--gap-2xl) var(--gap-s);
    --calendar-month-only-header-font-family: var(--font-family-system);
    --calendar-month-only-header-font-size: 16px;
    --calendar-month-only-header-font-weight: 400;

    /*  */
    --calendar-day-size: 40px;
    --calendar-day-border-radius: 4px;

    /* today */
    --calendar-today-border-color: var(--color-light-border-key);

    /* highlighted */
    --calendar-highlighted-color: var(--color-light-text-primary);
    --calendar-highlighted-background: var(--color-light-graphic-neutral);

    /* range */
    --calendar-range-color: var(--color-light-text-primary);
    --calendar-range-background: var(--color-static-graphic-solitude);

    /* selected */
    --calendar-selected-color: var(--color-light-text-primary-inverted);
    --calendar-selected-background: var(--color-light-graphic-primary);

    /* disabled */
    --calendar-disabled-color: var(--color-light-text-tertiary);
    --calendar-disabled-background: transparent;

    /* marker */
    --calendar-marker-background: var(--color-light-graphic-tertiary);
}
.daysTable {
    border-collapse: separate;
    border-spacing: 0;
    position: relative
}
.daysTable th {
        padding: 0;
    }
.daysTable td {
        padding: var(--gap-2xs) 0 0
    }
.daysTable td:first-child .transitLeft {
            background: -webkit-gradient(
                linear,
                right top, left top,
                from(var(--calendar-range-background)),
                to(var(--color-light-specialbg-nulled))
            );
            background: linear-gradient(
                270deg,
                var(--calendar-range-background) 0%,
                var(--color-light-specialbg-nulled) 100%
            )
        }
.daysTable td:first-child .transitLeft:after {
                display: none;
            }
.daysTable td:last-child .transitRight {
            background: -webkit-gradient(
                linear,
                right top, left top,
                from(var(--color-light-specialbg-nulled)),
                to(var(--calendar-range-background))
            );
            background: linear-gradient(
                270deg,
                var(--color-light-specialbg-nulled) 0%,
                var(--calendar-range-background) 100%
            )
        }
.daysTable td:last-child .transitRight:after {
                display: none;
            }
.dayName {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;

    height: 40px;
    color: var(--color-light-text-secondary);
}
.day {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;

    position: relative;
    width: 40px;
    height: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: var(--calendar-day-border-radius);
    color: var(--color-light-text-primary)
}
.day.event:before {
            content: '';
            display: block;
            position: absolute;
            bottom: 2px;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background: var(--calendar-marker-background);
        }
.day.today {
        font-weight: bold
    }
.day.today span:after {
            content: '';
            border: 1px solid var(--calendar-today-border-color);
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            border-radius: var(--calendar-day-border-radius);
            -webkit-box-sizing: border-box;
                    box-sizing: border-box;
        }
.day.highlighted {
        color: var(--calendar-highlighted-color);
        background: var(--calendar-highlighted-background);
    }
.day.disabled {
        pointer-events: none;
        color: var(--calendar-disabled-color);

        background: var(--calendar-disabled-background);
        cursor: default;
    }
.day.range {
        color: var(--calendar-range-color);
        background: var(--calendar-range-background);
        border-radius: 0;
    }
.day.selected {
        color: var(--calendar-selected-color);
        background: var(--calendar-selected-background);
        cursor: default;
    }
.day.rangeStart {
        cursor: pointer;
    }
.day.firstDay:after, .day.lastDay:after {
            -webkit-transition: opacity 0.2s ease;
            transition: opacity 0.2s ease;
            content: '';
            position: absolute;
            display: block;
            width: 100%;
            height: 100%;
            opacity: 0;
            pointer-events: none;
        }
.day.firstDay:after {
            right: 100%;
            background: -webkit-gradient(
                linear,
                right top, left top,
                from(var(--calendar-range-background)),
                to(var(--color-light-specialbg-nulled))
            );
            background: linear-gradient(
                270deg,
                var(--calendar-range-background) 0%,
                var(--color-light-specialbg-nulled) 100%
            );
        }
.day.lastDay:after {
            left: 100%;
            background: -webkit-gradient(
                linear,
                right top, left top,
                from(var(--color-light-specialbg-nulled)),
                to(var(--calendar-range-background))
            );
            background: linear-gradient(
                270deg,
                var(--color-light-specialbg-nulled) 0%,
                var(--calendar-range-background) 100%
            );
        }
.day.transitLeft:after, .day.transitRight:after {
            opacity: 1;
        }
.day:focus {
        z-index: 1;
    }
.left .daysEnter {
        -webkit-transform: translateX(var(--calendar-day-size));
                transform: translateX(var(--calendar-day-size));
    }
.left .daysEnterActive {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
.left .daysExit {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
.left .daysExitActive {
        -webkit-transform: translateX(calc(-1 * var(--calendar-day-size)));
                transform: translateX(calc(-1 * var(--calendar-day-size)));
    }
.right .daysEnter {
        -webkit-transform: translateX(calc(-1 * var(--calendar-day-size)));
                transform: translateX(calc(-1 * var(--calendar-day-size)));
    }
.right .daysEnterActive {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
.right .daysExit {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
.right .daysExitActive {
        -webkit-transform: translateX(var(--calendar-day-size));
                transform: translateX(var(--calendar-day-size));
    }
.daysEnter {
    opacity: 0;
    position: absolute;
    left: 0;
}
.daysEnterActive {
    opacity: 1;
}
.daysExit {
    pointer-events: none;
    opacity: 1;
}
.daysExitActive {
    opacity: 0;
}
.daysEnterActive,
.daysExitActive {
    -webkit-transition: opacity 300ms, -webkit-transform 300ms;
    transition: opacity 300ms, -webkit-transform 300ms;
    transition: opacity 300ms, transform 300ms;
    transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}
